import React, { Component } from "react";

// reactstrap components
import { Row, Col, Card, CardBody, CardHeader, CardTitle } from "reactstrap";

import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
/* Use `/dist/cjs/` if youre not in ESM! */
import { a11yDark } from "react-syntax-highlighter/dist/esm/styles/prism";

// core components
import PanelHeader from "../../components/PanelHeader/PanelHeader.js";

import { CodeExamples } from "../../variables/codeExamples";

class Code extends Component {
  render() {
    return (
      <>
        <PanelHeader size="sm" />
        <div className="content">
          <Row>
            <Col md="9">
              <Card>
                <CardHeader>
                  <CardTitle>Documentao dos aplicativos</CardTitle>
                  <p className="text-muted">
                    Padres e boas prticas que devem ser seguidos nos projetos
                    mobile
                  </p>

                  <h5>Em relao ao cdigo</h5>
                </CardHeader>

                <CardBody>
                  <p>
                    <h6>
                      <a id="tip1">
                        - O uso de constantes deve ser aproveitado
                      </a>
                    </h6>

                    <p class="font-weight-bold">Bad Practice</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={CodeExamples.badP1}
                      />
                    </pre>

                    <p class="font-weight-bold">Best Practice</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={CodeExamples.bestP1}
                      />
                    </pre>
                  </p>

                  <p class="mt-5">
                    <h6>
                      <a id="tip2">- Mantenha o cdigo bem edentado</a>
                    </h6>

                    <p class="font-weight-bold">Bad Practice</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={CodeExamples.badP2}
                      />
                    </pre>

                    <p class="font-weight-bold">Best Practice</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={CodeExamples.bestP2}
                      />
                    </pre>
                  </p>

                  <p class="mt-5">
                    <h6>
                      <a id="tip3">
                        - Separe os mtodos com espaos e margens horizontais
                      </a>
                    </h6>

                    <p class="font-weight-bold">Bad Practice</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={CodeExamples.badP3}
                      />
                    </pre>

                    <p class="font-weight-bold">Best Practice</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={CodeExamples.bestP3}
                      />
                    </pre>
                  </p>

                  <p class="mt-5">
                    <h6>
                      <a id="tip4">- Ordene os mtodos por importncia</a>
                    </h6>

                    <p>
                      Chamada de hooks (UseEffect, useCallback, etc) devem vir
                      primeiro, por terem extrema influncia no fluxo da
                      aplicao.
                    </p>

                    <p class="font-weight-bold">Bad Practice</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={CodeExamples.badP4}
                      />
                    </pre>

                    <p class="font-weight-bold">Best Practice</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={CodeExamples.bestP4}
                      />
                    </pre>
                  </p>

                  <p class="mt-5">
                    <h6>
                      <a id="tip5">
                        - Evite o uso excessivo de console.logs no projeto
                      </a>
                    </h6>

                    <p>
                      Todos ns utilizamos console.logs para debugar o cdigo e
                      encontrar erros, porm procure criar o hbito de apagar
                      todos os console.logs que no so mais usados, isso ajuda
                      com que outros desenvolvedores ou voc mesmo, consiga
                      enxergar os logs realmente necessrios de um modo muito
                      mais fcil.
                    </p>
                  </p>

                  <p class="mt-5">
                    <h6>
                      <a id="tip6">
                        - Evite a utilizao de hardcode ao definir estilos
                      </a>
                    </h6>

                    <p class="font-weight-bold">Bad Practice</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={CodeExamples.badP5}
                      />
                    </pre>

                    <p class="font-weight-bold">Best Practice</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={CodeExamples.bestP5}
                      />
                    </pre>
                  </p>

                  <p class="mt-5">
                    <h6>
                      <a id="tip6">- Preze sempre a reutilizao de cdigo</a>
                    </h6>

                    <p>
                      Sempre que um estilo for utilizado em mais de um arquivo
                      do projeto, crie um <b>StyledComponents</b> para ele.
                    </p>

                    <p>
                      <b>OBS.:</b> Os pxels utilizados dentro de um componente{" "}
                      <b>styled</b> so automaticamente convertidos para
                      pxels-independentes-de-densidade, "dp". Por isso, aqui a
                      utilizao da unidade "px"  vlida.
                    </p>

                    <p class="font-weight-bold">Exemplo de StyledComponents</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={CodeExamples.bestP6}
                      />
                    </pre>

                    <p>
                      Sempre que um grupo de components e estilos tiverem a
                      necessidade de serem reutilizados, crie um{" "}
                      <b>Component</b>.
                    </p>

                    <p class="font-weight-bold">Exemplo de Components</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={CodeExamples.bestP7}
                      />
                    </pre>
                  </p>
                </CardBody>
              </Card>
            </Col>

            <Col
              style={{
                position: "fixed",
                top: "11%",
                right: "0px",
                width: "22%",
              }}
            >
              <Card className="card-user">
                <CardHeader>
                  <CardTitle>Guia Rpido</CardTitle>
                </CardHeader>

                <CardBody>
                  <p>
                    <a className="text-muted" href="#tip1">
                      - O uso de constantes deve ser aproveitado
                    </a>
                  </p>

                  <p>
                    <a className="text-muted" href="#tip2">
                      - Mantenha o cdigo bem edentado
                    </a>
                  </p>

                  <p>
                    <a className="text-muted" href="#tip3">
                      - Separe os mtodos com espaos e margens horizontais
                    </a>
                  </p>

                  <p>
                    <a className="text-muted" href="#tip4">
                      - Ordene os mtodos por importncia
                    </a>
                  </p>

                  <p>
                    <a className="text-muted" href="#tip5">
                      - Evite o uso excessivo de console.logs no projeto
                    </a>
                  </p>

                  <p>
                    <a className="text-muted" href="#tip6">
                      - Evite a utilizao de hardcode ao definir estilos
                    </a>
                  </p>

                  <p>
                    <a className="text-muted" href="#tip7">
                      - Preze sempre a reutilizao de cdigo
                    </a>
                  </p>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </div>
      </>
    );
  }
}

export default Code;
